 /* Root Elements  */

body{
    margin:0;
    padding: 0;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: #e4e4e4;
}

#titleInputBox,#userInputBox {
    resize: none;
}

/* Input Fields */

.card {
    display: flex;
    flex-direction: column;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 91vw;
    align-items: center;
    margin: auto;
    margin-top: 2rem;
    height: auto;
    background-color: #ffffff;
}
.heading-1, .heading-2{
    font-family: 'Varela Round', sans-serif;
    font-size: 1.5rem;
    text-align: center;
    margin: 0;
    margin-top: 10px;
    margin-bottom: 10px;
}
#titleInputBox{
    width: 88vw;
    height: 2rem;
    border: 1px solid #e2e2e2;
  
    
}
#userInputBox{
    width: 88vw;
    height: 250px;
    border: 1px solid #e2e2e2;

}
#titleInputBox:focus, #userInputBox:focus {
    outline: none;
}
#titleInputBox:hover, #userInputBox:hover{
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
    transition: 0.2s;
}

#titleInputBox::placeholder{
    text-align: center;
    padding-top: 8px;
    font-family: 'Varela Round', sans-serif;
    color: rgb(117, 117, 117);

}
#userInputBox::placeholder{
    text-align: center;
    padding-top: 7.5rem;
    font-family: 'Varela Round', sans-serif;
    color: rgb(117, 117, 117);
}



/* Buttons */

.button{
    font-family: 'Varela Round', sans-serif; 
    border: none;
    background: #404040;
    color: #ffffff !important;
    font-weight: 100;
    font-size: 1rem;
    padding: 15px;
    text-transform: uppercase;
    border-radius: 25px;
    display: inline-block;
    transition: all 0.3s ease 0s;
    margin-left: 1rem;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;

}
.button:hover {
    color: #404040 !important;
    font-weight: 700 !important;
    
    letter-spacing: 3px;
    background: none;
    -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    transition: all 0.3s ease 0s;
    cursor: pointer;
}

.button:focus{

    outline:none;
}

.btn-Small{
    color: #ffffff;
    font-weight: 700 !important;
    font-size: 200%;
    background-color: #404040;
    height: 2.5rem;
    width: 91vw;
    text-align: center;
    padding-top:3px;
    cursor: pointer;
}

.btn-Small:hover{
    color:#404040;
    background-color: #a7a7a7;
    transition: all 0.3s ease 0s;
}


/*Display Notes Section */

.container{
    /*margin-top: 2rem;*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-self: center ;
    margin-bottom: 2rem;
    
}

.cardSmall {
    display: flex;
    flex-direction: column;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
    transition: 0.2s;
    width: 91vw;
    overflow-x: hidden;
    overflow-y: auto;
    margin-top: 2rem;
    height: auto;
    background-color: #ffffff;
}
.cardSmall:hover{
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.2s;
    width: 92vw;
    
}


.noteName{
    font-size: large;
    font-family: 'Varela Round', sans-serif;
    margin:10px;
    padding-bottom: 5px;
    border-bottom: 1px solid #979797;
    width: auto ;
    text-align: center;
}
.savedNotes{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: small;
    padding:1rem;
    height: auto;
    max-height: 200px;
    overflow-y: auto;
}



/* SCROLLBAR */

#style-10::-webkit-scrollbar-track
{
	background-color: #ffffff;
	border-radius: 10px;
}

#style-10::-webkit-scrollbar
{
	width: 10px;
	background-color: #F5F5F5;
}

#style-10::-webkit-scrollbar-thumb
{
	background-color: #AAA;
	border-radius: 10px;
	background-image: -webkit-linear-gradient(90deg,
	                                          rgba(0, 0, 0, .2) 25%,
											  transparent 25%,
											  transparent 50%,
											  rgba(0, 0, 0, .2) 50%,
											  rgba(0, 0, 0, .2) 75%,
											  transparent 75%,
											  transparent)
}




.cardSmall i{
    float: right;
    margin-right: 10px;
    margin-left: 0.5rem;
    color: #525252;
}

.cardSmall i:hover{
    color: #c5c5c5;
    cursor: pointer;
}


/* Unselect the text  */

i , button , textarea 
{
    -webkit-user-select: none;  /* Chrome all / Safari all */
    -moz-user-select: none;     /* Firefox all */
    -ms-user-select: none;      /* IE 10+ */
    user-select: none;          /* Likely future */      
  }