.toolbox{
    background-color: #ffffff;
    width: 90.5vw;
    height: 36px;
    margin-top: 2rem;
    align-self: center;
    position: relative;
    left:4.5%;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    
}
.toolbox ul{
    list-style: none;
    display: flex;
    float: right;
    
}
.toolbox ul li i {
    width: 10px;
    height: 10px;
    margin-right: 1rem;
    margin-top: 10px;

}
.toolbox ul li textarea{
    resize: none;
    width: 300px;
    height: 20px;
    margin-top: 10px;
    outline: none;
    border: none;
    padding-left: 5vw;
    
}

/*** Hovers **/
.toolbox ul li i:hover{
    color: #6b6b6b;
    cursor: pointer;
}

.toolbox ul li textarea::placeholder{
    font-family: 'Varela Round', sans-serif;
}
